.header {
  position: relative;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.consultation__link {
  color: var(--accent-color);
  text-transform: uppercase;
  display: block;
  background-color: transparent;
  border: 2px solid var(--accent-color);
  text-align: center;
}

//0-1679
@media screen and (max-width: 1679px) {
  .menu-site-nav {
    display: none;
  }
  .mobile-menu-btn {
    background-color: transparent;
    line-height: 0;
    padding: 0;
    border: none;
    fill: #000000;
    transition: fill 250ms linear;

    &:hover,
    &:focus {
      fill: var(--accent-color);
    }
  }
  .mobile-menu {
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    overflow: auto;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    background-color: rgba(17, 21, 24, 0.9);
    transition: opacity 250ms linear, visibility 250ms linear;

    &.is-open {
      opacity: 1;
      visibility: visible;
      pointer-events: auto;
    }
  }

  .mobile-menu__close-icon {
    margin: 0;
  }

  .mobile-menu__close-btn {
    position: absolute;
    top: 20px;
    right: 15px;
    width: 20px;
    height: 20px;
    background-color: transparent;
    text-align: center;
    line-height: 0;
    padding: 0;
    border: none;
    fill: var(--dark-theme-text-color);
    transition: fill 250ms linear;

    &:hover,
    &:focus {
      fill: var(--accent-color);
    }
  }
  .mobile-menu-container {
    position: relative;
    display: flex;
    flex-direction: column;
  }
  .mobile-menu-site-nav {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .mobile-menu-site-nav__link {
    color: var(--dark-theme-text-color);
    @include fonts(800, 20px, 36px);
    letter-spacing: 0.02em;
    transition: color 250ms linear;

    &.current {
      color: var(--accent-color);
    }

    &:hover,
    &:focus {
      color: var(--accent-color);
    }
  }
}

//0-767
@media screen and (max-width: 767px) {
  .header {
    padding-top: 20px;
    margin-bottom: 40px;
  }

  .mobile-menu-container {
    padding-top: 20px;
  }

  .mobile-menu-site-nav {
    margin: 40px 0;
    height: 144px;
  }

  .menu-consultation {
    display: none;
  }

  .consultation__link {
    @include fonts(700, 12px, 15px);
    letter-spacing: 0.08em;
    height: 48px;
    padding: 14px 0;
  }
}

//768 +
@media screen and (min-width: 768px) {
  .header {
    padding-top: 40px;
    margin-bottom: 80px;
  }

  .mobile-menu-container {
    padding-top: 40px;
  }

  .mobile-menu-btn {
    top: 50px;
    right: 15px;
  }
  .mobile-menu__close-btn {
    top: 53px;
  }

  .mobile-menu-site-nav {
    align-items: flex-start;
    width: 332px;
    height: 188px;
    margin: 80px auto 0 auto;
  }
  .mobile-menu-site-nav__link {
    @include fonts(800, 26px, 47px);
    letter-spacing: 0.03em;
  }

  .mobile-consultation {
    display: none;
  }

  .consultation__link {
    @include fonts(700, 14px, 17px);
    letter-spacing: 0.14em;
    height: 48px;
    width: 260px;
    margin-left: auto;
    margin-right: 40px;
    padding: 15px 0;
  }
}

//1680 +
@media screen and (min-width: 1680px) {
  .mobile-menu-btn,
  .mobile-menu {
    display: none;
  }
  .consultation__link {
    height: 70px;
    width: 336px;
    margin-right: 0;
    margin-left: 40px;
    padding: 26px 0;
  }

  .menu-site-nav {
    display: flex;
    margin-left: auto;
  }
  .menu-site-nav__link {
    color: var(--secondary-text-color);
    @include fonts(700, 14px, 17px);
    text-transform: uppercase;
    transition: color 250ms linear;

    &.current {
      color: var(--accent-color);
    }

    &:hover,
    &:focus {
      color: var(--accent-color);
    }
  }
  .menu-site-nav__item {
    margin-right: 40px;

    &:nth-child(4n) {
      margin-right: 0;
    }
  }
}
